import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { Card, CardBody, CardFooter, CardText, CardTitle, Container } from 'reactstrap';
import productApi from '../../../../api/productApi';
import { useParams } from 'react-router-dom';
import convert from '../../../../converter/converCurrency';

ViewPage.propTypes = {

};

function ViewPage(props) {

    const [product, setProduct] = useState(null);
    const { productId } = useParams();
    useEffect(() => {
        const getData = async () => {
            try {
                const res = await productApi.findById(productId);
                setProduct(res);
            } catch (error) {
                console.log(error);
            }
        }
        getData();
    }, []);

    return (
        <Container>
            {product !== null ?
                <Card>
                    <CardTitle>{product.name}</CardTitle>
                    <CardBody>
                        <CardText>Giá: {convert.toVND(product.price)}</CardText>
                        <CardText>Số lượng: {product.quantity}</CardText>
                    </CardBody>
                    <CardFooter>
                        Danh mục: {product.category.name}
                    </CardFooter>
                </Card> : ""
            }
        </Container>
    );
}

export default ViewPage;